.single-blog-wrapper * {
  margin: revert;
  padding: revert;
  list-style-type: revert;
}
.single-blog-wrapper p {
  line-height: 1.25rem;
}
.single-blog-wrapper code, .single-blog-wrapper pre {
  border-radius: 5px;
  background-color: #282c34;
  color: #abb2bf;
}
.single-blog-wrapper code {
  padding: 2px 5px;
}
.single-blog-wrapper pre {
  border: 1px solid #CCCCCC;
  line-height: 1.25em;
  overflow: auto;
  padding: 6px 10px;
}
.single-blog-wrapper pre code {
  display: block;
}
.single-blog-wrapper blockquote {
  margin: 0;
  border-left: 5px solid #7a7a7a;
  font-style: italic;
  padding: 1.33em;
  text-align: left;
}
.single-blog-wrapper table {
  padding: 1rem 0;
}
.single-blog-wrapper th, .single-blog-wrapper td {
  padding: 6px 13px;
  border: 1px solid black;
}
.single-blog-wrapper hr {
  background: black;
}

